import React from 'react'
import Helmet from 'react-helmet'
import Link from 'gatsby-link'
// import OwlCarousel from 'react-owl-carousel'

class Exercise extends React.Component {
  constructor({ props, data }) {
    super(props)
    this.goBack = this.goBack.bind(this)
    this.post = data.markdownRemark;
  }

  goBack() {
    this.props.history.goBack()
  }

  render() {
    return (
      <div className="exercise-page">
      <Helmet title={`Fitness Map - ${this.post.frontmatter.title}`} />
      <div className="exercise-post">
        <button onClick={this.goBack} className="btn-back">
          <svg className="preview-btn-icon">
            <use xlinkHref="/sprite.svg#icon-keyboard_arrow_left" />
          </svg>Назад
        </button>
        <div className="exercise-path">
        <span>
          <svg className="preview-btn-icon preview-btn-icon-dark">
            <use xlinkHref="/sprite.svg#icon-keyboard_arrow_right" />
          </svg>
        </span>
          <Link to={`/`}>Все упражнения</Link>
        <span>
          <svg className="preview-btn-icon preview-btn-icon-dark">
            <use xlinkHref="/sprite.svg#icon-keyboard_arrow_right" />
          </svg>
        </span>
          <Link to={`/bodypart/${this.post.frontmatter.filterby}`}>
            {this.post.frontmatter.bodypart}
          </Link>
          <span>
            <svg className="preview-btn-icon preview-btn-icon-dark">
              <use xlinkHref="/sprite.svg#icon-keyboard_arrow_right" />
            </svg>
          </span>
        </div>
        <h1 className="exercise-title">{this.post.frontmatter.title}</h1>
        {/* <OwlCarousel className="exercise-images" autoplay dots rewind loop margin={5} items={1}>
          <img className="exercise-image item" src={this.post.frontmatter.image} />
          <img className="exercise-image item" src={this.post.frontmatter.image_2} />
          <img className="exercise-image item" src={this.post.frontmatter.image_3} />
        </OwlCarousel> */}
        <span className="preview-stats">
          <Link to={`/bodypart/${this.post.frontmatter.filterby}`} className="tag tag-bodypart">
            {this.post.frontmatter.bodypart}
          </Link>
          <Link to={`/type/${this.post.frontmatter.filtertype}`} className="tag tag-type">
            {this.post.frontmatter.type}
          </Link>
          <Link to={`/inventory/${this.post.frontmatter.filterinventory}`} className="tag tag-inventory">
            {this.post.frontmatter.inventory}
          </Link>
          </span>

        <div>
          <Link to={`/stats/efficiency`} className="tag tag-efficiency">
            <svg className="tag-icon tag-icon-efficiency">
              <use xlinkHref="/sprite.svg#icon-check-circle" />
          </svg>{' '}{this.post.frontmatter.efficiency}/10
          </Link>
          <Link to={`/stats/injury`} className="tag tag-injury">
            <svg className="tag-icon tag-icon-injury">
              <use xlinkHref="/sprite.svg#icon-wheelchair" />
            </svg>{' '}{this.post.frontmatter.injury}/10
          </Link>
        </div>

        <div className="exercise-content" dangerouslySetInnerHTML={{ __html: this.post.html }} />  
        <button onClick={this.goBack} className="btn-back">
          <svg className="preview-btn-icon">
            <use xlinkHref="/sprite.svg#icon-keyboard_arrow_left" />
          </svg>Назад
        </button>
      </div>
    </div>
    );
  }
}

export default Exercise

export const pageQuery = graphql`
  query ExerciseByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
        image
        image_2
        image_3
        image_4
        filterby
        efficiency
        injury
        bodypart
        filtertype
        type
        filterinventory
        inventory
        filterplace
      }
    }
  }
`;



